<template>
  <div id="configwifi" :style="{height:fullHeight + 'px'}">
    <!-- :style="{height:fullHeight + 'px'}" -->
       <head-top title="Configure WiFi" go-back="true"></head-top>
       <div class="content" :style="{marginTop: -offsetTop + 'px'}">
          <div class="outletwifi" >
            <img src="../../../assets/addevice/wifi@3x.png" />
          </div>
          <div class="getwifi" ref="getwifi">
            <div class="wifiname">
                <input type="text" v-model="WIFIValue" placeholder="WiFi Name"  @focus="screamsize" @blur="scream"/>
                <!-- <img src="../../../assets/addevice/Next@3x.png" /> -->
            </div>
            <div class="wifipassword">
                <input v-model="WIFIPassword" :type="passwordHide?'password':'text'" placeholder="WiFi Password" ref="password" maxlength="16" @focus="screamsize" @blur="scream">
                <img src="../../../assets/addevice/eye-close@3x.png" v-if="passwordHide" @click="hide"/>
                <img src="../../../assets/addevice/Eye-open@3x.png" v-if="!passwordHide" @click="hide" />
            </div>
          </div>
          <div class="nextbtn" @click="next" v-show="!keyboard">
            <span>Next</span>
          </div>
          <div class="prompt" v-show="!keyboard">
            <p>Tips:<br/>
              <span>1.  If the router does not have a password, it does not need to enter the WiFi password.</span><br/>
              <span>2. No support for WIFI in the 5G band</span>
            </p>
          </div>
      </div>
  </div>
</template>
<script>
import { mapState, mapGetters } from 'vuex'
import { WiFiNameRequest, WiFiConfigRequest } from '../../../api/method.js'
import { WiFiNameResponse, WiFiConfigResponse } from '../../../api/getData.js'
import headTop from '../../../components/common/head'

export default {
  data () {
    return {
      timer: null, // 定时器
      fullHeight: document.documentElement.clientHeight, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移）
      WIFIValue: null, // wifi名
      WIFIPassword: '', // wifi密码
      passwordHide: true // 默认隐藏密码
    }
  },
  mounted () {
    let h = 0
    this.fullHeight = document.documentElement.clientHeight
    window.onresize = () => {
      h = document.documentElement.clientHeight
      console.log(h)
      console.log(this.fullHeight)
      console.log('20180829')
      // 软键盘是否弹起
      if (h < this.fullHeight) {
        this.keyboard = true
        this.$refs.getwifi.style.marginTop = '-45px'
      } else {
        this.keyboard = false
        this.$refs.getwifi.style.marginTop = '48px'
      }
      // 解决小屏幕遮挡输入框
      if (h < this.fullHeight) {
        this.offsetTop = this.fullHeight - h
      } else {
        this.offsetTop = 0
      }
    }

    // 获取wifi名称返回
    WiFiNameResponse(this.NAVIGATOR, this)
    if (this.$root.ISDEBUG) {
      window.WiFiNameResponse('XSSSink_TP_LINK_2.4G')
    }
    // 配置WiFi网络返回
    WiFiConfigResponse(this.NAVIGATOR, this)

    // 获取wifi名称
    WiFiNameRequest(this.NAVIGATOR, this)
  },

  components: {
    headTop
  },
  computed: {
    ...mapState([
      'transitiondre'
    ]),
    ...mapGetters(['currentRouter'])
  },

  methods: {
    next () {
      if (this.$root.ISDEBUG) {
        window.WiFiConfigResponse(true)
      }
      // 配置wifi网络
      WiFiConfigRequest(this.NAVIGATOR, this, this.WIFIValue, this.WIFIPassword)
    },
    screamsize () {
      this.$refs.getwifi.style.marginTop = '-45px'
    },
    scream () {
      this.$refs.getwifi.style.marginTop = '48px'
    },
    hide () {
      this.passwordHide = !this.passwordHide
      this.timer = setTimeout(() => {
        this.$refs.password.focus()
      }, 10)
    },
    navjump () {
      this.$store.state.transitiondre = false
      this.$router.go(-1)
    }
  },
  destroyed () {
    clearTimeout(this.timer)
  },
  watch: {
    fullHeight (val) {
      console.log('屏幕高度变化')
      console.log(val)
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../../styles/mixin.less';
  #configwifi{
   .fs;
   z-index: 1;
   background: #fff;
   transition: all .1s ease-in;
   overflow: hidden;
   .wh(100%,100%);
   .content{
     overflow: hidden;
     .wh(100%, auto);
   }
   .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
   .outletwifi{
     margin: 50px auto 0 auto;
     display: flex;
     justify-content: center;
      img{
        .wh(216px,326px)
      }
    }
    .getwifi{
      width: 620px;
      margin: 0 auto;
      margin-top: 96px;
      input{
        flex: 1;
        outline: none;
        height: 66px;
      }
      .wifiname{
        display: flex;
        border-bottom: 1px solid @themeColor;
        padding-top: 34px;
        height: 102px;
      }
      .wifipassword, .wifiname{
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid @themeColor;
        height: 102px;
        padding-top: 34px;
        img{
          .wh(40px, 32px);
          align-self: center;
        }
      }

    }
   .prompt{
      width: 650px;
      margin: 0 auto;
      margin-top: 100px;
      line-height: 30px;
      color: #ACACAC;
      font-size: 23px;
      text-align: left;
    }
    .nextbtn{
      .wh-l(520px,100px);
      margin: 0 auto;
      margin-top: 70px;
      border-radius:10px;
      background: #13af68;
      text-align: center;
      span{
        font-size: 47px;
        color: #fff;
        vertical-align: middle
      }
    }
  }
 </style>



